
.btn-wrapper[data-v-12] {
  margin-bottom: 16px;
}
.btn[data-v-12] {
  border-width: 2px;
  border-style: solid;
  padding: 16px 32px;
  cursor: pointer;
  font-size: 20px;
  font-weight: bold;
  transition: transform 100ms ease-in-out;
  box-sizing: border-box;
}
.btn[data-v-12]:hover {
  transform: scale(1.1);
  transition: transform 100ms ease-in-out;
}



