
.slide-left-enter-active[data-v-10] {
  position: absolute;
  right: 0;
  transition: right 0.5s ease;
  background: red;
}
.slide-left-leave-active[data-v-10] {
  position: absolute;
  left: -100%;
  opacity: 0;
  transition: left 0.5s ease, opacity 0.5s ease;
  background: green;
}
.slide-left-enter[data-v-10] {
  right: -100%;
}
.slide-left-leave[data-v-10] {
  left: 0;
}
.slide-right-enter-active[data-v-10] {
  position: absolute;
  left: 0;
  transition: left 0.5s ease;
  background: red;
}
.slide-right-leave-active[data-v-10] {
  position: absolute;
  right: -100%;
  transition: right 0.5s ease;
  background: green;
}
.slide-right-enter[data-v-10] {
  left: -100%;
}
.slide-right-leave[data-v-10] {
  right: 0;
}



