
html {
  height: 100%;
}
body {
  height: 100%;
}
h1, .heading {
  font-size: 36px;
  font-weight: bold;
}
h2, .header {
  font-size: 28px;
  font-weight: bold;
}
h3, .sub-header {
  font-size: 20px;
  font-weight: normal;
}
p {
  font-size: 16px;
}
img {
  width: 100%;
}
.action {
  margin: 36px auto;
}
#app {
  font-family: sans-serif;
  text-align: center;
}
.container {
  max-width: 640px;
  margin: 0 auto;
  z-index: 1;
  padding: 0 14px;
  background: white;
  position: relative;
}
.black-overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  background: black;
  transition: opacity 250ms ease-in-out;
}
.black-overlay.active {
  opacity: 0.7;
  z-index: 50;
  visibility: visible;
  transition: opacity 250ms ease-in-out;
}




